<template>
<div>

  <el-breadcrumb separator=">">
    <span class="el-breadcrumb__item"><span></span></span>
    <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
    <el-breadcrumb-item></el-breadcrumb-item>
    <el-breadcrumb-item></el-breadcrumb-item>
  </el-breadcrumb>

  <el-row class="jm-grid-box-border-radius jm-grid-box-bg-white jm-grid-border-1px jm-padding-15X20 jm-margin-top20 jm-margin-bottom20 primary-timer-filter">
    <el-col :span="12">
      <div class="grid-content">
        <span></span>
        <el-button :type="latest7Days" @click="selectDatePeriod(7)">7</el-button>
        <el-button :type="latest15Days" @click="selectDatePeriod(15)">15</el-button>
        <el-button :type="latest30Days" @click="selectDatePeriod(30)">30</el-button>
      </div>
    </el-col>
    <el-col :span="12">
      <div class="grid-content clearfix">
        <el-date-picker ref="dateRangePicker" class="right" :editable="false" :clearable="false" v-model="global_time_filter_value"
          type="daterange" :picker-options="globalPickerOptions" placeholder="" @change="dateChanged" align="right">
        </el-date-picker>
      </div>
    </el-col>
  </el-row>
  <!-- end of time filter-->

  <el-row>
    <el-col :span="24">
      <div class="grid-content jm-grid-box-bg-white jm-grid-border-1px zhibiao-activeTabName">
        <el-tabs v-model="zhibiao_activeTabName" type="card" @tab-click="handle_zhibiao_Click" v-loading="loadingIndex" element-loading-text="...">
          <el-tab-pane label="PV" name="pv">

            <div class="box chart home-row-group-1 sixteen-nine">
              <div class="content " ref='pvChart'>

              </div>
            </div>

          </el-tab-pane>
          <el-tab-pane label="UV" name="uv">

            <div class="box chart home-row-group-1 sixteen-nine">
              <div class="content " ref='uvChart'>

              </div>
            </div>

          </el-tab-pane>
          <el-tab-pane label="IP" name="ip">

            <div class="box chart home-row-group-1 sixteen-nine">
              <div class="content " ref='ipChart'>

              </div>
            </div>

          </el-tab-pane>
          <el-tab-pane label="" name="averageTime">

            <div class="box chart home-row-group-1 sixteen-nine">
              <div class="content " ref='averageTimeChart'>

              </div>
            </div>

          </el-tab-pane>
          <el-tab-pane label="" name="avgVisitPages">

            <div class="box chart home-row-group-1 sixteen-nine">
              <div class="content " ref='avgVisitPagesChart'>

              </div>
            </div>

          </el-tab-pane>
          <el-tab-pane label="" name="exitRate">

            <div class="box chart home-row-group-1 sixteen-nine">
              <div class="content" ref='exitRateChart'>

              </div>
            </div>

          </el-tab-pane>
        </el-tabs>

        <el-tooltip class="item" popper-class="zhibiao-tooltip" content="" placement="top-end">
            <span class="jm-tooltip-icon"></span>
          </el-tooltip>
      </div>
    </el-col>

  </el-row>
  <!-- end of zhibiao-->

  <el-row :gutter="20">
    <el-col :xs="24" :sm="24" :md="24" :lg="12">
      <div class="grid-content jm-grid-box-bg-white jm-grid-border-1px">
        <header class="panel-header jm-grid-border-bottom-1px">
          <el-tooltip class="item" content="TOP5" placement="top-end">
            <span class="jm-tooltip-icon"></span>
          </el-tooltip>
        </header>

        <div class="box sixteen-nine" v-loading="loadingYmllpmChart" element-loading-text="...">
          <div class="content" ref='ymllpmChart'>

          </div>
        </div>
      </div>
    </el-col>
    <el-col :xs="24" :sm="24" :md="24" :lg="12">
      <div class="grid-content jm-grid-box-bg-white jm-grid-border-1px">
        <header class="panel-header jm-grid-border-bottom-1px">
          <el-tooltip class="item" content="TOP5" placement="top-end">
            <span class="jm-tooltip-icon"></span>
          </el-tooltip>
        </header>

        <div class="box sixteen-nine" v-loading="loadingYmtclpmChart" element-loading-text="...">
          <div class="content" ref='ymtclpmChart'>

          </div>
        </div>

      </div>
    </el-col>
  </el-row>
  <!-- end of  &  -->

  <el-row :gutter="20">
    <el-col :xs="24" :sm="24" :md="24" :lg="12">
      <div class="grid-content jm-grid-box-bg-white jm-grid-border-1px">
        <header class="panel-header jm-grid-border-bottom-1px">
          <el-tooltip class="item" content="" placement="top-end">
            <span class="jm-tooltip-icon"></span>
          </el-tooltip>
        </header>

        <div class="box sixteen-nine">
          <div class="content">
            <section class="new-old-visitor" v-loading="loadingNewOldVisitor" element-loading-text="...">
              <ul class="clearfix">
                <li class="left jm-width-50-p"><span class="lable"></span><span class="new-percentage">{{tableData.newPercent}}</span></li>
                <li class="left jm-width-50-p"><span class="lable"></span><span class="old-percentage">{{tableData.oldPercent}}</span></li>
              </ul>

              <div class="new-old-visitor-detail">

                <el-table :data="tableData.tableData" :show-header="false" border style="width: 100%">
                  <el-table-column prop="name" label="type">
                  </el-table-column>
                  <el-table-column prop="newNumber" label="new">
                  </el-table-column>
                  <el-table-column prop="oldNumber" label="old">
                  </el-table-column>
                </el-table>
              </div>

            </section>
          </div>

        </div>
      </div>
    </el-col>
    <el-col :xs="24" :sm="24" :md="24" :lg="12">
      <div class="grid-content jm-grid-box-bg-white jm-grid-border-1px">
        <header class="panel-header jm-grid-border-bottom-1px">
          <el-tooltip class="item" content="" placement="top-end">
            <span class="jm-tooltip-icon"></span>
          </el-tooltip>
        </header>

        <div class="box sixteen-nine" v-loading="loadingDyfbChart" element-loading-text="...">
          <div class="content" ref='dyfbChart'>

          </div>
        </div>

      </div>
    </el-col>
  </el-row>
  <!-- end of  &  -->

  <el-row class="jm-grid-border-1px">
    <header class="panel-header jm-grid-box-bg-white jm-grid-border-bottom-1px">
      <el-tooltip class="item" content="" placement="top-end">
        <span class="jm-tooltip-icon"></span>
      </el-tooltip>
    </header>
    <el-col :xs="24" :sm="24" :md="24" :lg="16">
      <div class="grid-content jm-grid-box-bg-white">

        <div class="box chart row-group-last" v-loading="loadingFwlyLineChart" element-loading-text="...">
          <div class="content" ref='fwlyLineChart'>

          </div>

        </div>
      </div>
    </el-col>
    <el-col :xs="24" :sm="24" :md="24" :lg="8">
      <div class="grid-content jm-grid-box-bg-white">

        <div class="box  chart row-group-last" v-loading="loadingFwlyLineChart" element-loading-text="...">
          <div class="content" ref='fwlyPieChart'>

          </div>
        </div>

      </div>
    </el-col>
  </el-row>

</div>


</template>
<script>
import {
  baiduTrend,
  pageVisit,
  getSources,
  getVistors,
  getAreas
} from '@/service/api'
import util from '@/service/util'

import echarts from 'echarts';
// https://segmentfault.com/q/1010000008182097
import 'echarts/map/js/china'

export default {
  name: 'home',
  created() {
    // 7
    const start = new Date();
    start.setTime(start.getTime() - 3600 * 1000 * 24 * 6);
    this.global_time_filter_value[0] = start;
  },
  methods: {
    dateChanged() {
      this.loadAllData();
    },
    loadAllData() {
      var vm = this;

      var platform = vm.platformId,
        startDate = vm.global_time_filter_value[0].format(),
        endDate = vm.global_time_filter_value[1].format();

      vm.loadTabContentChart(vm.zhibiao_activeTabName)

      vm.loadingYmllpmChart = true
      vm.loadingYmtclpmChart = true
      pageVisit({
          platform,
          startDate,
          endDate
        }).then(function (response) {
          vm.loadingYmllpmChart = false
          vm.loadingYmtclpmChart = false
          var chartData = response.data
          vm.loadymllpmChart(chartData)
          vm.loadymtclpmChart(chartData)
        })
        .catch(function (error) {
          console.log(error);
        });

      vm.loadingNewOldVisitor = true
      getVistors({
          platform,
          startDate,
          endDate
        }).then(function (response) {
          vm.loadingNewOldVisitor = false
          var tableData = response.data
          vm.tableData = tableData
        })
        .catch(function (error) {
          console.log(error);
        });

      vm.loadingDyfbChart = true
      getAreas({
          platform,
          startDate,
          endDate
        }).then(function (response) {
          vm.loadingDyfbChart = false
          var chartData = response.data
          vm.loaddyfbChart(chartData)
        })
        .catch(function (error) {
          console.log(error);
        });

      vm.loadingFwlyLineChart = true
      getSources({
          platform,
          startDate,
          endDate
        }).then(function (response) {
          vm.loadingFwlyLineChart = false
          var chartData = response.data
          vm.loadfwlyLineChart(chartData)
          vm.loadfwlyPieChart(chartData)
        })
        .catch(function (error) {
          console.log(error);
        });
    },
    // 
    loadfwlyLineChart(chartData) {
      var vm = this;
      // domecharts
      vm.fwlyLineChart = echarts.init(vm.$refs.fwlyLineChart);

      var lineChart = chartData.lineChart
      var colors = ["#44cd8a", "#5eb2ec", "#b6a4dd"]
      var series = []
      for (let index = 0; index < lineChart.legendData.length; index++) {
        var _index = index > 2 ? 0 : index;
        series.push({
          name: lineChart.legendData[index],
          type: 'line',
          smooth: true,
          itemStyle: {
            normal: {
              color: colors[_index]
            }
          },
          data: lineChart.seriesData[index]
        })
      }

      // 
      var option = {
        tooltip: {
          trigger: 'axis'
        },
        legend: {
          top: '2%',
          data: lineChart.legendData
        },
        grid: {
          left: '3%',
          right: '4%',
          bottom: '5%',
          containLabel: true
        },
        xAxis: {
          type: 'category',
          boundaryGap: false,
          axisLine: {
            lineStyle: {
              color: '#158bca'
            }
          },
          data: lineChart.xAxisData
        },
        yAxis: {
          type: 'value',
          axisLine: {
            lineStyle: {
              color: '#158bca'
            }
          }
        },
        series: series
      };

      vm.fwlyLineChart.setOption(option)
    },

    // 
    loadfwlyPieChart(chartData) {
      var vm = this;
      // domecharts
      vm.fwlyPieChart = echarts.init(vm.$refs.fwlyPieChart);

      var pieChart = chartData.pieChart
      // 
      var option = {
        tooltip: {
          trigger: 'item',
          formatter: "{a} <br/>{b}: {c} ({d}%)"
        },
        legend: {
          orient: 'vertical',
          top: 'middle',
          right: '5%',
          data: pieChart.legendData
        },
        series: [{
          name: '',
          type: 'pie',
          center: ['40%', '50%'],
          radius: ['40%', '60%'],
          avoidLabelOverlap: false,
          label: {
            normal: {
              show: false,
              position: 'center'
            },
            emphasis: {
              show: true,
              textStyle: {
                fontSize: '30',
                fontWeight: 'bold'
              }
            }
          },
          labelLine: {
            normal: {
              show: false
            }
          },
          color: ['#44cd8a', '#5eb2ec', '#b6a2dc'],
          data: pieChart.seriesData
        }]
      };

      vm.fwlyPieChart.setOption(option)
    },
    // 
    loaddyfbChart(chartData) {
      var vm = this;
      // domecharts
      vm.dyfbChart = echarts.init(vm.$refs.dyfbChart);


      // 
      var option = {
        title: {
          text: '',
          left: '15px',
          top: '19px',
          textStyle: {
            fontSize: 12,
            fontWeight: 'normal'
          }

        },
        tooltip: {
          trigger: 'item'
        },
        visualMap: {
          orient: 'horizontal',
          inverse: 'true',
          min: 0,
          max: chartData.max,
          left: '65px',
          top: '15px',
          text: ['', ''], // 
          inRange: {
            color: ['#e0ffff', '#006edd']
          }
        },
        geo: {
          map: 'china',
          roam: true,
          label: {
            normal: {
              show: true,
              textStyle: {
                color: 'rgba(0,0,0,0.4)'
              }
            }
          },
          itemStyle: {
            normal: {
              borderColor: 'rgba(0, 0, 0, 0.2)'
            },
            emphasis: {
              areaColor: null,
              shadowOffsetX: 0,
              shadowOffsetY: 0,
              shadowBlur: 20,
              borderWidth: 0,
              shadowColor: 'rgba(0, 0, 0, 0.5)'
            }
          }
        },
        series: [{
          name: '',
          type: 'map',
          geoIndex: 0,
          data: chartData.data
        }]
      };

      vm.dyfbChart.setOption(option)
    },
    // 
    loadymllpmChart(chartData) {
      var vm = this;
      // domecharts
      vm.ymllpmChart = echarts.init(vm.$refs.ymllpmChart);

      var pvChartData = chartData.pvChart

      // 
      var option = {
        tooltip: {
          trigger: 'axis',
          axisPointer: {
            type: 'shadow'
          }
        },
        grid: {
          top: '8%',
          left: '5%',
          right: '5%',
          bottom: '8%',
          containLabel: true
        },
        xAxis: {
          type: 'value',
          boundaryGap: false,
          axisLine: {
            lineStyle: {
              color: '#158bca'
            }
          }
        },
        yAxis: {
          type: 'category',
          splitLine: {
            show: true
          },
          axisLine: {
            lineStyle: {
              color: '#158bca'
            }
          },
          axisLabel: {
            formatter: function (value, index) {
              var value = value || ''
              var maxLength = 15
              if (value.length > maxLength) {
                value = value.substring(0, maxLength) + '...';
              }
              return value;
            },
            rotate: 20
          },
          data: pvChartData.yAxisData.reverse()
        },
        series: [{
          name: pvChartData.seriesName,
          type: 'bar',
          barWidth: '20px',
          itemStyle: {
            normal: {
              color: '#5eb2ed',
              barBorderRadius: 2
            }
          },
          data: pvChartData.seriesData.reverse()
        }]
      };

      vm.ymllpmChart.setOption(option)

    },
    // 
    loadymtclpmChart(chartData) {
      var vm = this
      // domecharts
      vm.ymtclpmChart = echarts.init(vm.$refs.ymtclpmChart);

      var existCountChart = chartData.existCountChart
      // 
      var option = {
        tooltip: {
          trigger: 'axis',
          axisPointer: {
            type: 'shadow'
          }
        },
        grid: {
          top: '8%',
          left: '5%',
          right: '5%',
          bottom: '8%',
          containLabel: true
        },
        xAxis: {
          type: 'value',
          boundaryGap: false,
          axisLine: {
            lineStyle: {
              color: '#158bca'
            }
          }
        },
        yAxis: {
          type: 'category',
          splitLine: {
            show: true
          },
          axisLine: {
            lineStyle: {
              color: '#158bca'
            }
          },
          axisLabel: {
            formatter: function (value, index) {
              var value = value || ''
              var maxLength = 15
              if (value.length > maxLength) {
                value = value.substring(0, maxLength) + '...';
              }
              return value;
            },
            rotate: 20
          },
          data: existCountChart.yAxisData.reverse()
        },
        series: [{
          name: existCountChart.seriesName,
          type: 'bar',
          barWidth: '20px',
          itemStyle: {
            normal: {
              color: '#44cd8a',
              barBorderRadius: 2
            }
          },
          data: existCountChart.seriesData.reverse()
        }]
      };

      vm.ymtclpmChart.setOption(option)
    },
    selectDatePeriod(period) {

      switch (period) {
        case 7:
          this.latest7Days = 'primary'
          this.latest15Days = ''
          this.latest30Days = ''
          break;
        case 15:
          this.latest7Days = ''
          this.latest15Days = 'primary'
          this.latest30Days = ''
          break;
        case 30:
          this.latest7Days = ''
          this.latest15Days = ''
          this.latest30Days = 'primary'
          break;
      }

      const end = new Date();
      const start = new Date();
      start.setTime(start.getTime() - 3600 * 1000 * 24 * (period-1));

      this.global_time_filter_value = [start, end]

      this.loadAllData()
    },
    handle_zhibiao_Click(tab, event) {
      var vm = this;

      var id = tab.name;


      setTimeout(function () {
        vm.loadTabContentChart(id, false)
      }, 200);

    },

    loadTabContentChart(id, reload = true) {
      var vm = this;
      var chartId = id + 'Chart';
      vm[chartId] = echarts.init(vm.$refs[chartId]);

      var platform = vm.platformId;

      var startDate = vm.global_time_filter_value[0].format(),
        endDate = vm.global_time_filter_value[1].format();

      if (reload || vm.indexList === null) {
        vm.loadingIndex = true
        baiduTrend({
            platform,
            startDate,
            endDate
          }).then(function (response) {
            vm.loadingIndex = false
            var chartData = response.data
            vm.indexList = chartData;
            loadTabChartOption(chartData)

          })
          .catch(function (error) {
            console.log(error);
          });

      } else {
        var chartData = vm.indexList
        loadTabChartOption(chartData)
      }

      function loadTabChartOption(chartData) {

        var xAxisData = chartData.xAxisData

        var loadingChartData = chartData[chartId]

        // 
        var option = {
          tooltip: {
            trigger: 'axis'
          },
          legend: {
            data: [loadingChartData.seriesName]
          },
          grid: {
            left: '3%',
            right: '3%',
            bottom: '10%',
            containLabel: true
          },
          xAxis: [{
            type: 'category',
            boundaryGap: false,
            axisLine: {
              lineStyle: {
                color: '#158bca'
              }
            },
            data: xAxisData
          }],
          yAxis: [{
            name: loadingChartData.unit,
            type: 'value',
            axisLine: {
              lineStyle: {
                color: '#158bca'
              }
            }
          }],
          series: [{
              name: loadingChartData.seriesName,
              type: 'line',
              smooth: true,
              itemStyle: {
                normal: {
                  color: '#5eb2ed'
                }
              },
              areaStyle: {
                normal: {
                  color: '#acd7f4'
                }
              },
              data: loadingChartData.seriesData
            }

          ]
        };
        vm[chartId].setOption(option)
      }
    }

  },
  mounted() {

    var vm = this;

    vm.loadAllData()

    window.addEventListener('resize', chartsResize, false);
    window.addEventListener('resize', fixSearchBar, false);
    window.addEventListener('scroll', fixSearchBar, false);

    var searchFilter = document.getElementsByClassName('primary-timer-filter')[0];

    function fixSearchBar() {

      if (document.body.scrollTop > 113) {
        searchFilter.style.position = 'fixed'
        searchFilter.style.top = '40px'
        searchFilter.style.left = '192px'
        searchFilter.style.right = '20px'
        searchFilter.style.zIndex = 40000
      } else {
        searchFilter.style.position = 'static'
        searchFilter.style.top = ''
        searchFilter.style.left = ''
        searchFilter.style.right = ''

      }


    }
    var resizeTimer;

    function chartsResize() {
      if (resizeTimer) {
        clearTimeout(resizeTimer)
      }
      resizeTimer = setTimeout(function() {
        var tabContentChart = `${vm.zhibiao_activeTabName}Chart`
        if (vm[tabContentChart]) {
          vm[tabContentChart].resize()
        }

        vm.ymllpmChart && vm.ymllpmChart.resize()
        vm.ymtclpmChart && vm.ymtclpmChart.resize()
        vm.dyfbChart && vm.dyfbChart.resize()
        vm.fwlyLineChart && vm.fwlyLineChart.resize()
        vm.fwlyPieChart && vm.fwlyPieChart.resize()
      }, 100);
      
    }
  },
  data() {
    return {
      loadingIndex: false,
      loadingYmllpmChart: false,
      loadingYmtclpmChart: false,
      loadingNewOldVisitor: false,
      loadingDyfbChart: false,
      loadingFwlyLineChart: false,
      indexList: null,
      platformId: '102000',
      latest7Days: 'primary',
      latest15Days: '',
      latest30Days: '',
      zhibiao_activeTabName: 'pv',
      global_time_filter_value: [new Date(), new Date()],
      globalPickerOptions: {
        shortcuts: [{
          text: '7',
          onClick(picker) {
            setPickerDate(picker, 6)
          }
        }, {
          text: '15',
          onClick(picker) {
            setPickerDate(picker, 14)
          }
        }, {
          text: '30',
          onClick(picker) {
            setPickerDate(picker, 29)
          }
        }]
      },
      tableData: []
    }
  }
}

function setPickerDate(picker, period) {
  const end = new Date();
  const start = new Date();
  start.setTime(start.getTime() - 3600 * 1000 * 24 * period);
  picker.$emit('pick', [start, end]);
}


</script>

<style lang="scss">

.zhibiao-tooltip {
  width: 300px;
}

.zhibiao-activeTabName {
  position: relative;
  .el-tooltip {
    position: absolute;
    display: block;
    width: 17px;
    height: 17px;
    padding: 0 10px 0 5px;
    top: 22px;
    right: 30px;
    margin-top: -8px;
    cursor: pointer;
    background: url(../assets/images/icon-tooltip.png) no-repeat center;
  }
}

  .primary-timer-filter {
    .el-button {
      padding-top: 8px;
      padding-bottom: 8px;
    }
  }

  .new-old-visitor *,
  .new-old-visitor *:before,
  .new-old-visitor *:after {
    -webkit-box-sizing: border-box;
    -moz-box-sizing: border-box;
    box-sizing: border-box;
  }

  .new-old-visitor {
    text-align: center;
    li {
      line-height: 56px;
      list-style: none;
    }
    li+li {
      border-left: 1px solid #d9d9d9;
    }
    .label {
      vertical-align: middle;
      color: #666666;
    }
    .new-percentage {
      vertical-align: middle;
      font-size: 30px;
      color: #23ad44;
    }
    .old-percentage {
      vertical-align: middle;
      font-size: 30px;
      color: #0588e6;
    }
  }

  .new-old-visitor-detail {
    padding: 0 20px 30px;
  }

</style>



