/**
 * 
 */
export function firstLevelCustoms() {
  var instance = axios.create({
    baseURL: ''
  });
  // /v1/dict/firstLevelCustoms
  let url = `/v1/dict/firstLevelCustoms`
  return instance.get(url)
}

